import { kebabCase } from 'change-case';
export function solvePluginName(pluginName) {
    const fileName = kebabCase(pluginName);
    const folderName = pluginName[2].toLowerCase() + pluginName.replace(/^lg\w/, '');
    return `${folderName}/${fileName}`;
}
export const handleResult = (result) => result.status == 'fulfilled' ? result.value.default : console.error('lightGallery', result.reason);
